<template>
  <div id="timeline" class="">
    <h1>Curiculum Vitae</h1>
    <div class="controls">


    <scrolldown
      class="scroll-element"
      :class="{ hidden: currentIndex <= 0 }"
      @scroll="emitDate(currentIndex - 1)"
      :type="'up'"
      :message="'SCROLL DOWN'"
    ></scrolldown>

    <div class="years">
      <button
        v-for="(date, index) in computedDates"
        :key="index"
        @click="emitDate(date.index)"
        class="date"
        :class="{ active: date.index == currentIndex }"
      >
        <span>{{ date.headline }}</span>
        <span>{{ date.display_value }}</span>
      </button>
    </div>

    <scrolldown
      class="scroll-element"
      :class="{ hidden: currentIndex >= computedDates.length - 1 }"
      @scroll="emitDate(currentIndex + 1)"
      :type="'down'"
      :message="'SCROLL UP'"
    ></scrolldown>
    </div>
  </div>
</template>

<script>
import scrolldown from "./scrolldown.vue";
export default {
  components: {
    scrolldown: scrolldown
  },
  data: function () {
    return {};
  },
  computed: {
    computedDates: function () {
        var date = this.dates
        return date;
      
    },
    currentDate: function () {
      return this.computedDates[this.currentIndex];
    },
  },

  props: ["dates", "currentIndex"],

  created: function () {
    //this.currentDate = this.computedDates[0]
  },

  methods: {
    log: function (input) {
      console.log(input);
    },
    emitDate: function (dateIndex) {
      this.$emit("setContent", dateIndex);
    },
  },
};
</script>

