  padding: 1rem 2rem;
  margin: 1em 0;
  border-radius: 1em;
  border: 2px solid var(--grey);
  background-color: transparent;
  color: var(--grey);
  font-weight: bold;
  display: flex;
  justify-content: space-between;
}

.date.active {
  border-color: var(--highlight);
  color: var(--highlight);
}

.date:hover {
  background-color: var(--inactive);
}

.date span {
  display: inline-block;
  white-space: nowrap;
}

@media screen and (max-width: 780px){

  #timeline{
      background-color: var(--secondary-bg);
      z-index: 10;
      box-shadow: 0 0 20px var(--shadow);
  }

  .date{
    display:none;
  }

  .years{
    flex-grow:1;
  }

  .date.active{
    display:block;
  }
  
  .controls{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
  }

}

</style>