<template>
  <div class="social-skills">
    <div class="circle">
      <svg
        class="circle-chart"
        :viewbox="'0 0 ' + width + ' ' +height"
        :width="width"
        :height="height"
        xmlns="http://www.w3.org/2000/svg"
      >
        <circle
          class="circle-chart__background"
          :stroke="inactiveColor"
          stroke-width="10"
          fill="none"
          :cx="x"
          :cy="y"
          :r="radius"
        />
        <circle
          class="circle-chart__circle"
          :stroke="higlightColor"
          stroke-width="10"
          :stroke-dasharray="(circumference / 100) * level"
          stroke-linecap="round"
          fill="none"
          :cx="x"
          :cy="y"
          :r="radius"
        />
      </svg>
      <span>{{label}}</span>
    </div>
  </div>
</template>

<script>
export default {

props: ['level', 'label'],

data: function(){
    return {
        higlightColor: null,
        inactiveColor: null,
        width: 140
    }
},

computed: {
    radius: function(){return (this.width - 10) / 2},
    circumference: function(){
        return 2 * Math.PI * this.radius;
    },
    height: function(){return this.width},
    x: function(){return this.width/2},
    y: function(){return this.width/2}
    
},

created: function(){
var style = getComputedStyle(document.body)
this.higlightColor = style.getPropertyValue('--highlight');
this.inactiveColor = style.getPropertyValue('--inactive');
}

};
</script>

<style>

.circle-chart__circle {
  transform: rotate(-90deg);
  transform-origin: center;
}

.circle span{
    display: block;
    text-align: center;
    padding: var(--default-p);
}

.circle{
    padding: var(--default-p);
    text-align: center;
}
</style>