<template>
<div id="sidebar" class="deep-shadow">

  <nav>
    <a v-for="(item, index) in navigation" 
    :href="item.link"
    class="nav-el"
    @click="changeNav(index)">
    <i class="fa highlight" :class="[ {active: activeEl.label === item.label}, item.icon]"></i>
    <span>{{item.label}}</span>
    </a>

  </nav>
</div>

</template>

<script>
export default {
    name: 'sidebar',
    props: ['navigation', 'currentNav'],
    data: function(){
      return {
        
      }
    },
    computed: {
        activeEl: function(){
          return this.navigation[this.currentNav]
        }
    },
    methods: {
      changeNav: function(navIndex){
        this.$emit('changeComponent', navIndex);
      }
    }
}
</script>

