<template>
  <div id="cv">
    <timeline
      :dates="dates"
      :currentIndex="currentContent.index"
      @setContent="changeContent($event)"
    ></timeline>
    <div class="content">
      <div class="component">
        <transition name="component-fade" mode="out-in">
          <component
            v-if="currentContent"
            :is="currentContent.name"
          ></component>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import Timeline from "./Timeline.vue";
import school from "./cv_stages/school.vue";
import universityStart from "./cv_stages/universityStart.vue";
import masterDegree from "./cv_stages/masterDegree.vue";
import ArrtySea from "./cv_stages/ArrtySea.vue";
import productOwner from "./cv_stages/ProductOwner.vue";
export default {
  components: {
    timeline: Timeline,
    school: school,
    universityStart: universityStart,
    ArrtySea: ArrtySea,
    productOwner: productOwner,
    masterDegree: masterDegree,
  },
  data: function () {
    return {
      contentList: [],
      currentContent: null,
    };
  },

  computed: {
    dates: function () {
      return this.contentList.map(function (content, index) {
        return {
          month: content.month,
          year: content.year,
          index: index,
          headline: content.headline,
        };
      });
    },
  },

  methods: {
    changeContent: function(evt){
      this.currentContent = this.contentList[evt];
    }
  },

  created: function () {
    const content_components = [
      school,
      universityStart,
      ArrtySea,
      productOwner,
      masterDegree,
    ];
    var arr = [];
    content_components.forEach(function (cc, index) {
      var obj = cc.data();
      obj["name"] = cc.name;
      obj["index"] = index;
      arr.push(obj);
    });
    this.contentList =  arr;
    this.currentContent = this.contentList[0];
  },

};
</script>

