  display: flex;
  flex-direction: row;
  align-items: stretch;
  flex-grow: 1;
  overflow-y: auto;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  flex-grow: 1;
}

.component {
  margin: auto;
  flex-grow: 1;
  overflow-y: auto;
  width: 100%;
}

.scroll-element {
  transition: opacity 1s ease;
}

.hidden {
  opacity: 0;
  cursor: auto;
}

